.header {
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.25);
    padding: 28px 0 26px;
    background-color: #fff;
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;

}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;

    position: relative;
}

.nav__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media screen and (max-width: 640px) {
        display: none;
    }
}

.nav__widgets {
    display: flex;
    gap: 18px;
}
